body {
  margin: 0;
  background-color: #eee;
  font-family: 'Fira Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
}
body, h1, h2, h3, h4, h5, h6, p, a, li, span, div {
  font-family: 'Fira Sans', sans-serif;
  font-weight: 300;
}
